    private setCurrentLocation() {
        this.geoLocationService.getPosition().subscribe((pos: any) => {
            this.lat = +pos.coords.latitude;
            this.lng = +pos.coords.longitude;
            this.zoom = 12;
            this.radiusLat = this.lat;
            this.radiusLong = this.lng;
        });
    }

    setMarkerAtLocation() {
        
        this.stationMarker = [];
        let params = {
            connector_type_id: 0,
            lat: this.lat,
            lng: this.lng,
            radius: 5,
        };
        this.chargingStations = [];
        this.localStorage = JSON.parse(localStorage.getItem("userDetails"));
        // const gpu = new GPU();
        // const gpuMatrix = gpu.createKernel(function(){})
        if (
            this.localStorage.role_code == "EU" ||
            this.localStorage.role_code == null
        ) {
            this.chargingStationService
                .getChargingStationsByUserRoleAndLatLongUW(
                    this.localStorage.userId,
                    params
                )
                .subscribe(
                    (data: any) => {
                        this.preloader.close();
                        if (data.count > 0) {
                            let tempStation = data.data;
                            this.chargingStations = data.data;
                            this.getChargingStation();
                            let unit = "K";
                            for (var i = 0; i < tempStation.length; i++) {
                                if (
                                    this.lat == tempStation[i].lat &&
                                    this.lng == tempStation[i].lng
                                ) {
                                    //return 0;
                                } else {
                                    var radlat1 = (Math.PI * this.lat) / 180;
                                    var radlat2 =
                                        (Math.PI * tempStation[i].lat) / 180;
                                    var theta = this.lng - tempStation[i].lng;
                                    var radtheta = (Math.PI * theta) / 180;
                                    var dist =
                                        Math.sin(radlat1) * Math.sin(radlat2) +
                                        Math.cos(radlat1) *
                                            Math.cos(radlat2) *
                                            Math.cos(radtheta);
                                    if (dist > 1) {
                                        dist = 1;
                                    }
                                    dist = Math.acos(dist);
                                    dist = (dist * 180) / Math.PI;
                                    dist = dist * 60 * 1.1515;
                                    if (unit == "K") {
                                        dist = dist * 1.609344;
                                    }
                                }
                                if (dist <= this.circleMapRadius / 1000) {
                                    tempStation[i].infoWindowShow = true;
                                    this.stationMarker.push(tempStation[i]);
                                    this.close_infoWinow();
                                    if (this.stationMarker.length >= 50) {
                                        break;
                                    }
                                }
                            }
                        }
                    },
                    (err) => {}
                );
        } else {
            this.chargingStationService
                .getAllChargingStationsWithChargersAndConnectorsCCS(
                    this.localStorage.userId,
                    this.selectedClient_id,
                    this.selectedCpo_id,
                    this.selectedStation_id,
                    params
                )
                .subscribe(
                    (data: any) => {
                        this.preloader.close();
                        if (data.count > 0) {
                            let tempStation = data.data;
                            this.chargingStations = data.data;
                            this.getChargingStation();
                            let unit = "K";
                            for (var i = 0; i < tempStation.length; i++) {
                                if (
                                    this.lat == tempStation[i].lat &&
                                    this.lng == tempStation[i].lng
                                ) {
                                    //return 0;
                                } else {
                                    var radlat1 = (Math.PI * this.lat) / 180;
                                    var radlat2 =
                                        (Math.PI * tempStation[i].lat) / 180;
                                    var theta = this.lng - tempStation[i].lng;
                                    var radtheta = (Math.PI * theta) / 180;
                                    var dist =
                                        Math.sin(radlat1) * Math.sin(radlat2) +
                                        Math.cos(radlat1) *
                                            Math.cos(radlat2) *
                                            Math.cos(radtheta);
                                    if (dist > 1) {
                                        dist = 1;
                                    }
                                    dist = Math.acos(dist);
                                    dist = (dist * 180) / Math.PI;
                                    dist = dist * 60 * 1.1515;
                                    if (unit == "K") {
                                        dist = dist * 1.609344;
                                    }
                                }
                                if (dist <= this.circleMapRadius / 1000) {
                                    tempStation[i].infoWindowShow = true;
                                    this.stationMarker.push(tempStation[i]);
                                    this.close_infoWinow();
                                    // this.clickedMarker(event,this.chargingStations[i]);
                                    if (this.stationMarker.length >= 50) {
                                        break;
                                    }
                                }
                            }
                        }
                    },
                    (err) => {}
                );
        }
    }
